<template>
  <div class="container">
    <div class="flex">
      <div class="txt">上传证件照</div>
      <el-upload
        action="https://jsonplaceholder.typicode.com/posts/"
        list-type="picture-card"
        >
        <i class="el-icon-plus"></i>
      </el-upload>
    </div>
    <div class="flex">
      <div class="txt">职称</div>
      <input type="text" placeholder="请输入职称">
    </div>
    <div class="flex">
      <div class="txt">专业</div>
      <input type="text" placeholder="请输入专业">
    </div>
    <div class="flex">
      <div class="txt">年限</div>
      <input type="text" placeholder="请输入年限">
    </div>
    <div class="submit" @click="submit">申请</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
    }
  },

  components: {},

  computed: {},

  mounted: function() {},

  methods: {
    submit() {
      
    }
  }
}
</script>

<style lang='scss' scoped>
  .container {
    width: 400px;
    padding: 40px;
    .flex {
      padding: 10px;
      border-bottom: 1px solid #999;
      margin-bottom: 10px;
      align-items: center;
      input {
        border: none;
        text-align: right;
      }
    }
    .submit {
      margin-top: 50px;
      background: #2B4365;
      text-align: center;
      color: #fff;
      line-height: 40px;
      border-radius: 10px;
      cursor: pointer;
    }
  }
</style>



